/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/

/* Posts and pages */

//404
.four-content-area {
	max-width: 80%;
	margin: 0 auto;

	ul {
		margin-left: 0;
		padding: 10px;

		li {
			list-style-type: none;

			a {
				text-decoration: none;
				color: $darkGreen;
			}
		}
	}

	.search-text-four {
		p {
			color: $darkGreen;
		}
	}
}

.nothing-found-four {
	font-size: 1.5em;
	font-weight: bold;
}

.page-img-four-tent h1 {
	height: 80vh;
	width: 80vw;
	margin: 0 auto;
	background: url(../../images/tent-falling.gif) no-repeat;
	background-size: contain;
	color: rgb(175, 115, 5);
	text-align: left;
	padding: 30px;
	display: flex;
	flex-flow: row;
	align-items: flex-end;
}

//Header for all pages
.icon-search {
	color: $white;
	cursor: pointer;
	margin-right: 20px;
}

.header-search-bar {
	max-width: 200px;

	label {
		color: $white;
	}

	fieldset {
		width: 400px;
		text-align: center;
		border: none;
		padding: 17px;
		position: relative;
	}

	input {
		width: 0;
		opacity: 0;
		transition: all 1s;

		&:focus {
			color: $white;
		}
	}
}

::placeholder {
	color: $white;
	text-transform: uppercase;
}

.main-navigation {
	display: flex;
}

.search-form {
	display: flex;
}

.search-on label {
	input {
		width: 200px;
		opacity: 1;
		font-size: 15px;
		color: $white;
		background: transparent;
	}
}

.nav-header-flexbox {
	display: flex;
	position: absolute;
	justify-content: flex-start;
	width: 90%;
	z-index: 200;
	margin: 0 auto;
	border: none;
	align-items: center;
	.header-logo-img {
		height: 50px;
		width: 50px;
		margin-left: 40px;
		background: url(../../images/logos/inhabitent-logo-tent-white.svg) no-repeat;
		background-size: contain;
		margin-top: 10px;
		border: none;
	}

	.menu-menu-1-container {
		width: 100%;
	}

	ul {
		display: flex;
		justify-content: flex-end;
		align-items: center;
		padding: 20px;
		text-transform: uppercase;
		width: 100%;
		font-weight: bold;
		li {
			color: $white;
			
			// text-shadow: 1px 1px 2px #323232;
			a {
				font-family: 'Novecento Sans Web';
				font-weight: 400;
				color: $white;
				padding: 1rem;
				text-shadow: 1px 1px 2px #323232;
				

				&:hover {
					color: $orangeHover;
				}
			}
		}
	}
}

.no-large-image {
	
	.nav-back-color {
		border-bottom: solid 1px $black;
		margin-bottom: 10px;
		width: 100%;
		background: $background;
	}

	.nav-header-flexbox {
		position: relative;
		width: 90%;
		.header-logo-img {
			background: url(../../images/logos/inhabitent-logo-tent.svg) no-repeat;
			background-size: contain;
			margin-top: 10px;
			border: none;
		}

		ul {
			padding: 0;
			li {
				text-shadow: none;
				a {
					color: $darkGreen;
					text-decoration: none;
					text-shadow: none;

					&:hover {
						color: $black;
					}
				}
			}
		}
	}

	::placeholder {
		color: $darkGreen;
		text-transform: uppercase;
	}

	.icon-search {
		color: $darkGreen;
	}

	.header-search-bar {
		input {
			color: $darkGreen;

			&:focus {
				color: $darkGreen;
			}

		}
	}
}


//Front page
.main-img-logo {

	margin: 0;
	background:
		linear-gradient(to bottom, rgba(0, 0, 0, 0.3) 0%, rgba(0, 0, 0, 0.3) 100%),
		url(../../images/home-hero.jpg);
	background-size: cover, cover;
	height: 100vh;
	width: 100%;

	img {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
	}
}

//front page products 
.product-info {
	max-width: 1200px;
	margin: 0 auto;

	h2 {
		font-family: 'Novecento Sans Web';
		text-align: center;
		font-size: 40px;
		text-transform: uppercase;
		font-weight: normal;
		font-style: normal;
	}
}

.front-page-flex-box {
	display: flex;
	width: 100%;
	justify-content: space-around;
}

.front-page-product-item {
	width: 25%;
	display: flex;
	flex-flow: column;
	align-content: center;
	padding: 30px 10px;
	border: 1px solid #e1e1e1;
	border-top-width: 5px;
	margin: 0 20px;

	&:hover {
		border-color: $orangeHover;
	}

	img {
		width: 35%;
		margin: 0 auto;
	}

	p {
		margin: 0 auto;
		text-align: center;
		padding: 10px;
	}

	a {
		margin: 0 auto;
		padding: 10px;
		text-decoration: none;
		text-transform: uppercase;
		background: $darkGreen;
		color: $white;
	}
}

//Front-page Journal
.journal-info-container {
	max-width: 1200px;
	margin: 0 auto;

	h2 {
		font-family: Novecento Sans Web;
		text-align: center;
		font-size: 40px;
		text-transform: uppercase;
		font-weight: 400;
		font-style: normal;
	}
}

.front-page-journal-container {
	display: flex;
}

.front-page-post-container {
	width: 30%;
	margin: 15px;

}

.front-page-post-meta-container {
	display: flex;
	font-size: 0.8em;
	margin-top: 10px;
}

.front-page-journal-thumbnail {
	height: 245px;
	width: 370px;

	img {
		width: 100%;
		height: 100%;
	}
}

.front-page-post-title {

	a {
		text-transform: uppercase;
		text-decoration: none;
		color: $darkGreen;
		font-size: 1.4em;
		font-weight: bold;

		&:hover {
			color: $black;
		}
	}
}

.front-page-post-permalink {
	padding: 20px;
	position: relative;
	margin: 10px;

	a {
		position: absolute;
		transform: translate(-25px, -20px);
		padding: 10px;
		text-transform: uppercase;
		text-decoration: none;
		border: 1px solid $black;
		color: $black;

		&:hover {
			background: $darkGreen;
			color: $white;
		}
	}
}


//Front page more adventures
.more-adventures {
	position: relative;
	font-family: 'Merriweather Web';

	h1 {
		max-width: 1200px;
		margin: 60px auto;
		font-family: 'Merriweather Web';
		text-align: center;
		font-size: 40px;
		text-transform: uppercase;
		font-weight: 400;
		font-style: normal;
	}

	h2 {
		color: $white;
		font-size: 2.2em;
		margin-left: 20px;
		font-family: 'Merriweather Web';
		font-weight: 400;
	}

	a {
		text-decoration: none;
		color: $white;
		text-transform: uppercase;
		padding: 20px;
		border: solid 1px $white;
		position: absolute;
		margin-left: 20px;
		font-weight: bold;
		font-family: 'Novecento Sans Web';
		font-weight: 400;

		&:hover {
			color: $orangeHover;
		}
	}

	.front-page-more-adventure {
		a {
			position: absolute;
			background: $darkGreen;
			margin: 20px 0;

			&:hover {
				color: $black;
				background: $white;
				border: 1px solid $black;
			}
		}
	}
}

.grid-container-front-page {
	max-width: 1200px;
	margin: 0 auto;
	display: grid;
	grid-column: 50% 25% 25%;
	grid-row: 50% 50%;
	grid-gap: 10px;
	height: 700px;
	a:hover{
		background: $darkGreen;
		color: $white;
	}
}

.grid-item-canoe {
	grid-column: 1/2;
	grid-row: 1/3;
	background:
		linear-gradient(to bottom, rgba(0, 0, 0, 0.3) 0%, rgba(0, 0, 0, 0.3) 100%),
		url(../../images/adventure-photos/canoe-girl.jpg);
	background-size: cover, cover;

	a {
		top: 300px;
	}
}

.grid-item-night {
	grid-column: 2/4;
	grid-row: 1/2;
	background:
		linear-gradient(to bottom, rgba(0, 0, 0, 0.3) 0%, rgba(0, 0, 0, 0.3) 100%),
		url(../../images/adventure-photos/night-sky.jpg);
	background-size: cover, cover;
}

.grid-item-mountain {
	grid-column: 2/3;
	grid-row: 2/3;
	background:
		linear-gradient(to bottom, rgba(0, 0, 0, 0.3) 0%, rgba(0, 0, 0, 0.3) 100%),
		url(../../images/adventure-photos/mountain-hikers.jpg);
	background-size: cover, cover;
}

.grid-item-star {
	grid-column: 3/4;
	grid-row: 2/3;
	background:
		linear-gradient(to bottom, rgba(0, 0, 0, 0.3) 0%, rgba(0, 0, 0, 0.3) 100%),
		url(../../images/adventure-photos/beach-bonfire.jpg);
	background-size: cover, cover;

	a {
		top: 610px;
	}
}

//archive single category
.archive-page-main {
	max-width: 740px;
	margin: 30px auto;
}

div.archive-page-title {
	div.archive-single-category-description {
		font-weight: 100;
	}

	.month-title {
		//for the title of month page
		text-align: left;
		max-width: 740px;
		margin: 30px auto;
		padding: 20px;
		font-size: 1.8em;
	}
}


//archive product page
.archive-page-main {
	max-width: 1200px;
	margin: 0 auto;
}

.archive-page-title {
	text-align: center;
	text-transform: uppercase;
	font-weight: bold;
	letter-spacing: 0.2em;
}

.archive-page-nav {
	ul {
		max-width: 50%;
		display: flex;
		justify-content: space-around;
		list-style-type: none;
		margin: 30px auto;

		li {
			a {
				font-size: 1.2em;
				text-transform: uppercase;
				color: $darkGreen;
				font-family: 'Novecento Sans Web';
				font-weight: 400;
				text-decoration: none;
			}

		}
	}
}

.archive-product-grid {
	display: grid;
	grid-template-columns: repeat(4, 25%);
	grid-auto-rows: 1fr;
	grid-gap: 1rem;
	padding: 30px;

}


.archive-product-grid-item {
	img {
		widows: 100%;
		max-height: 180px;

	}
}

.grid-item-text {
	height: 4em;
	display: flex;
	justify-content: space-between;
	align-items: center;
	border: 1px solid #e1e1e1;
	border-top-width: 5px;
	padding: 0 10px;

	p {
		white-space: nowrap;
		margin: 0;
	}

	.dots {
		flex-grow: 1;
		overflow: hidden;

		&::after {
			content: '.............................................';
		}
	}
}

// Single product page
.single-product-container {
	max-width: 60%;
	margin: 0 auto;

	article {
		width: 100%;
	}
}

.single-product-flexbox {
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-top: 20px;


	.single-product-thumbnail {
		width: 50%;
		padding: 10px;
		display: flex;
		align-self: flex-start;
	}

	.single-product-text {
		width: 50%;
		padding: 10px;
		padding-top: 0;

		.entry-title {
			color: $orangeHover;
			text-transform: uppercase;
			margin-top: 0;
			padding-top: 0;
		}

		.single-product-price {
			font-size: 1.8em;
			font-family: Novecento Sans Web;
		}

		.entry-content {
			text-align: justify;
		}
	}
}


//Adventure Page
.adventure-page-header {
	display: none;

	h1 {
		display: none;
	}
}


.single-adventure-page {
	img {
		width: 100%;
		height: 100vh;
	}

	h2 {
		text-transform: uppercase;
		text-align: center;
		font-size: 2em;
	}

	p {
		width: 740px;
		margin: 0 auto;
		line-height: 30px;
		text-align: justify;
		padding: 20px;
	}
}

// About page
.hero-image-header {
	height: 100vh;
	width: 100%;
	color: $white;
	font-size: 1.2em;
	text-transform: uppercase;
}

.about-content {
	h3 {
		text-align: center;
		max-width: 740px;
		margin: 20px auto;
		font-size: 2em;
		text-transform: uppercase;
	}

	p {
		text-align: justify;
		max-width: 740px;
		margin: 20px auto;
	}
	:last-child{
		margin-bottom: 100px;
	}
	:nth-child(2) {
		margin-top: 80px;
	}
	:nth-child(5){
		margin-top: 50px;
	}
}

.about-title {
	display: none;
}


// Post page 
.article-journal {
	margin: 30px auto;
	max-width: 740px;
}

body article header.journal-header {
	position: relative;
	max-width: 640px;

	img {
		object-fit: fill;

	}
}

.journal-title a {
	background: $darkGreen;
	position: absolute;
	top: 40px;
	left: 0;
	color: $white;
	text-decoration: none;
	text-transform: uppercase;
	font-size: 34px;
	width: 450px;
	padding: 5px 20px;
}

.journal-meta {
	position: absolute;
	background: $darkGreen;
	bottom: 7px;
	right: 0;
	color: $white;
	padding: 2px 30px;
	margin: 0;
	text-transform: uppercase;
	font-family: 'Novecento Sans Web';
	font-weight: 400;
}

.journal-content {
	max-width: 640px;
	text-align: justify;
}

.journal-page-read-more {
	a {
		padding: 10px;
		text-transform: uppercase;
		text-decoration: none;
		border: 1px solid $black;
		color: $black;

		&:hover {
			background: $darkGreen;
			color: $white;
		}
	}
}

//Single page Journal
