.single-journal-comments-container {
	max-width: 740px;
	margin: 0 auto;
}

.comment-respond {
	h3 {
		text-transform: uppercase;
		letter-spacing: 0.2em;
		font-size: 1.7em;
	}
}

form {
	font-family: Novecento Sans Web;
	text-transform: uppercase;

