		display: none;
	}

	.form-submit input {
		text-transform: uppercase;
		background: $darkGreen;
		color: $white;
		font-family: Novecento Sans Web;
		box-shadow: none;

		&:hover {
			box-shadow: none;
			color: $orangeHover;
		}
	}

