	width: 60%;
	margin: 0 auto;

	.search-header {
		font-size: 1.5em;
		text-align: center;
	}

	.entry-title {
		a {
			text-decoration: none;
			color: $darkGreen;
			font-size: 1.5em;

			&:hover {
				color: $orangeHover;
			}
		}
	}

	.search-article .search-text {
		p {
			text-align: justify;
			font-size: 1.2em;
		}
	}

}

.search-not-fund-text {
	p {
		text-align: justify;
		font-size: 1.5em;
	}
}



///////////////
.sticky {
	display: block;
}

.hentry {
	margin: 0 0 $base__space-unit;
}

.updated:not(.published) {
	display: none;
}

.single .byline,
.group-blog .byline {
	display: inline;
}

.page-content,
.entry-summary {
	margin: $base__space-unit 0 0;
}

.page-links {
	clear: both;
	margin: 0 0 $base__space-unit;
}

/* Asides */

.blog .format-aside .entry-title,
.archive .format-aside .entry-title {
	display: none;
}

/* Comments */

.comment-content a {
	word-wrap: break-word;
}

.bypostauthor {
	display: block;
}